import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { Button, Card, CardBody, Col, Form, Modal, ModalBody, ModalFooter, ModalHeader, Row, Table, UncontrolledTooltip } from 'reactstrap';
import { ACTION_EVENTS, alertManager, AlertType, Box, InputGroupInline, ProcessBar, showToast, SUCCESS, Tree, WindowComponent } from 'summer';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { ContaFluxoCaixaIndireto } from '../../../../models/dtos/mcg/contaFluxoCaixaIndireto';
import { ContaFluxoCaixaIndiretoConta } from '../../../../models/dtos/mcg/contaFluxoCaixaIndiretoConta';
import { obterContasClassificacaoIndireto, salvarClassificacaoIndireto } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import PlanoContaIS from '../../../components/mcg/planoContaIS/PlanoContaIS';

const ContaFluxoCaixaIndiretoTreeView: FC<ContaFluxoCaixaIndireto> = () => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const [showModalView, setShowModalView] = useState(false);
  const [listaContaFluxoCaixa, setListaContaFluxoCaixa] = useState<ContaFluxoCaixaIndireto[]>([]);
  const { reset, ...formProps } = useForm<ContaFluxoCaixaIndireto>({ mode: 'onBlur' });
  const formPropsConta = useForm<ContaFluxoCaixaIndiretoConta>({ mode: 'onBlur' });
  const [contaFluxoIndiretoAtual, setContaFluxoIndiretoAtual] = useState<ContaFluxoCaixaIndireto>();
  const [tabela, setTabela] = useState();
  const [atualizado, setAtualizado] = useState();
  const [isBotaoAddDesabilitado, setIsBotaoAddDesabilitado] = useState(true);
  const [ehRenderizaTree, setEhRenderizaTree] = useState();
  const [listaDeNosPais, setListaDeNosPais] = useState([]);

  useEffect(
    () => {
      montarTree();
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const montarTree = () => {
    obterContasClassificacaoIndireto(globalReducer.globalParameter.grupoPlanoConta.id, {
      thenFunction: (result: ContaFluxoCaixaIndireto[]) => {
        const nos = [];
        result.forEach((item, index) => {
          if (item.tipo === 1 && item.subTipo === -1 && item.subSubTipo === -1) {
            nos.push(index);
            item.children = result.filter(possivelFilho => ehFilhoTipoUm(possivelFilho, item.tipo, item.subSubTipo));
          } else if (item.tipo === 1 && item.subTipo !== -1 && item.subSubTipo === -1) {
            item.children = result.filter(possivelFilho => ehFilhoTipoDois(possivelFilho, item.tipo, item.subTipo));
          } else if (item.tipo === 2) {
            nos.push(index);
            item.children = item.listConta;
          } else if (item.tipo === 3 && item.subTipo === -1) {
            nos.push(index);
            item.children = result.filter(possivelFilho => ehFilhoTipoTres(possivelFilho, item.tipo, item.subTipo));
          } else {
            item.children = item.listConta;
          }
        });
        setListaContaFluxoCaixa(result);
        setEhRenderizaTree(!ehRenderizaTree);
        setListaDeNosPais(nos);
      },
    });
  };

  const ehFilhoTipoUm = (possivelFilho, tipo, subSubTipo) => {
    return possivelFilho.tipo === tipo && possivelFilho.subTipo !== -1 && possivelFilho.subSubTipo === subSubTipo;
  };

  const ehFilhoTipoDois = (possivelFilho, tipo, subTipo) => {
    return possivelFilho.tipo === tipo && possivelFilho.subTipo === subTipo && possivelFilho.subSubTipo !== -1;
  };

  const ehFilhoTipoTres = (possivelFilho, tipo, subTipo) => {
    return possivelFilho.tipo === tipo && possivelFilho.subTipo !== subTipo;
  };

  const handleOpenModal = () => {
    return (e, id) => {
      e.preventDefault();
      const contaFluxoIndireto = listaContaFluxoCaixa.find(element => element.id === id);
      if (contaFluxoIndireto && instanceOfFluxoCaixaIndireto(contaFluxoIndireto)) {
        reset(contaFluxoIndireto);
        setContaFluxoIndiretoAtual(contaFluxoIndireto);
        setShowModalView(true);
      }
    };
  };

  const instanceOfFluxoCaixaIndireto = (object: any): object is ContaFluxoCaixaIndireto => {
    return 'listConta' in object;
  };

  const removerDaLista = (id: number) => {
    return () => {
      contaFluxoIndiretoAtual.listConta = contaFluxoIndiretoAtual.listConta.filter(element => element.planoConta.id !== id);
      setAtualizado(!atualizado);
    };
  };

  useEffect(
    () => {
      setTabela(
        contaFluxoIndiretoAtual ? (
          contaFluxoIndiretoAtual.listConta.map((item, index) => {
            return (
              <tr key={index}>
                <td>{item.planoConta.conta}</td>
                <td>{item.planoConta.codigoReduzido}</td>
                <td>{item.planoConta.descricao}</td>
                <td>{item.planoConta.analitica ? 'Sim' : 'Não'}</td>
                <td>
                  <Button id="removeButton" size="sm" color="link" className="text-danger" onClick={removerDaLista(item.planoConta.id)}>
                    <FontAwesomeIcon icon="trash" />
                  </Button>
                  <UncontrolledTooltip target="removeButton">Clique aqui para remover</UncontrolledTooltip>
                </td>
              </tr>
            );
          })
        ) : (
          <tr>
            <td />
            <td />
            <td />
            <td />
            <td />
          </tr>
        )
      );
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [contaFluxoIndiretoAtual, atualizado]
  );

  const addNaLista = () => {
    if (contaFluxoIndiretoAtual.listConta.find(element => element.planoConta.id === formPropsConta.getValues().planoConta.id)) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message:
          'Conta ' +
          formPropsConta.getValues().planoConta.conta +
          ' - ' +
          formPropsConta.getValues().planoConta.descricao +
          ' já foi adicionada na lista.',
        type: AlertType.ERROR,
      });
    } else {
      const contaFluxoCaixaIndiretoConta: ContaFluxoCaixaIndiretoConta = {
        planoConta: formPropsConta.getValues().planoConta,
      };
      contaFluxoIndiretoAtual.listConta.push(contaFluxoCaixaIndiretoConta);
      formPropsConta.setValue('planoConta', undefined);
      setIsBotaoAddDesabilitado(true);
      setAtualizado(!atualizado);
    }
  };

  const handleSubmit = () => {
    // regex verifica se o campo descricao está vazio ou se contém somente espaços em branco
    if (formProps.getValues().descricao.replace(/\s/g, '').length) {
      contaFluxoIndiretoAtual.descricao = formProps.getValues().descricao;
      salvarClassificacaoIndireto(contaFluxoIndiretoAtual, {
        thenFunction: () => {
          showToast('Registro alterado com sucesso!', SUCCESS);
          setShowModalView(false);
          formPropsConta.setValue('planoConta', undefined);
          setIsBotaoAddDesabilitado(true);
          montarTree();
        },
      });
    } else {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: 'Campo descrição não pode estar em branco',
        type: AlertType.ERROR,
      });
    }
  };

  const handleCloseModal = () => {
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message: 'As alterações da página atual serão perdidas, deseja continuar?',
      onOkClick: () => {
        setShowModalView(false);
        setIsBotaoAddDesabilitado(true);
        formPropsConta.setValue('planoConta', undefined);
        montarTree();
      },
      type: AlertType.WARNING_YES_NO,
    });
  };

  const controladorHabilitarPlanoConta = (itemAtual: ContaFluxoCaixaIndireto) => {
    let result: boolean;

    if (itemAtual.tipo === 1 && itemAtual.subSubTipo === -1) {
      result = true;
    } else if (itemAtual.tipo === 2) {
      result = false;
    } else {
      result = itemAtual.subTipo === -1;
    }

    return result;
  };

  const controladorHabilitarAddConta = () => {
    setIsBotaoAddDesabilitado(!formPropsConta.getValues().planoConta);
  };

  const retornoLabelFunc = () => {
    return (node: any) => {
      return (
        <div className={node.listConta ? 'font-weight-bold' : null}>
          {node.descricao || node.planoConta.conta + ' -> ' + node.planoConta.descricao}
        </div>
      );
    };
  };

  return (
    <WindowComponent
      title="Classificação das Contas de Fluxo de Caixa - Indireto"
      wikiId={787}
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasGrupoEmpresaConfig(globalReducer.globalParameter)]}
    >
      <Box>
        <Tree
          parentNodesIdList={listaDeNosPais}
          listenerRenderTree={ehRenderizaTree}
          nodeList={listaContaFluxoCaixa}
          labelFunc={retornoLabelFunc()}
          labelClickFunc={handleOpenModal()}
        />
      </Box>
      <Modal isOpen={showModalView} backdrop="static" centered={true} toggle={handleCloseModal} size="lg">
        <ModalHeader toggle={handleCloseModal}>Fluxo de Caixa - Classificação Contas</ModalHeader>
        <Form>
          <Card>
            <CardBody>
              <InputGroupInline id="id" formProps={formProps} hidden={true} />
              <Row>
                <Col>
                  <InputGroupInline formProps={formProps} id="descricao" isPrependLabel={true} label="Descrição" />
                </Col>
              </Row>
              <Row>
                <Col className="col-10">
                  <PlanoContaIS
                    disabled={contaFluxoIndiretoAtual ? controladorHabilitarPlanoConta(contaFluxoIndiretoAtual) : true}
                    id="planoConta"
                    formProps={{ ...formPropsConta, validation: { required: true } }}
                    label="Conta"
                    placeholder="Informe a Conta"
                    onChange={controladorHabilitarAddConta}
                  />
                </Col>
                <Col className="col-2">
                  <Button id="addButton" size="lg" color="link" className="text-success" disabled={isBotaoAddDesabilitado} onClick={addNaLista}>
                    <FontAwesomeIcon icon="plus-circle" />
                  </Button>
                  <UncontrolledTooltip target="addButton">Clique aqui para adicionar</UncontrolledTooltip>
                </Col>
              </Row>
              <Table style={{ textAlign: 'center' }} hover={true} striped={true} bordered={true}>
                <thead>
                  <tr>
                    <th style={{ width: '30%' }}>Conta</th>
                    <th style={{ width: '20%' }}>C.R.</th>
                    <th style={{ width: '30%' }}>Descrição</th>
                    <th style={{ width: '10%' }}>Analítica</th>
                    <th style={{ width: '10%' }}>Ações</th>
                  </tr>
                </thead>
                <tbody>{tabela}</tbody>
              </Table>
            </CardBody>
          </Card>
        </Form>
        <ModalBody />
        <ModalFooter>
          <Row>
            <Col>
              <ProcessBar handleSubmit={handleSubmit} handleClose={handleCloseModal} />
            </Col>
          </Row>
        </ModalFooter>
      </Modal>
    </WindowComponent>
  );
};

export default ContaFluxoCaixaIndiretoTreeView;
